.pop-menu {
  /* 底部距离点击处至少一个文字以及一个三角箭头的高度，即 20 + 6 */
  margin-top: -26px;
  position: fixed;
  background-color: #333;
  border-radius: 5px;
  transform: translate(-50%, -100%);
}

.pop-menu::after {
  content: '';
  width: 8px;
  height: 8px;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%) rotate(45deg);
  background-color: #333;
}

.pop-menu--item {
  padding: 5px 10px;
  white-space: nowrap;
  cursor: pointer;
}
