.panel {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.panel-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px 0 0 10px;
  padding: 10px;
  background: #333;
}

.line {
  width: 100%;
  padding-bottom: 10px;
  height: 1px;
  transform: scaleY(0.5);
  background-clip: content-box;
  background-color: #666;
}

