.shape-option {
  width: 28px;
  height: 28px;
  font-size: 18px;
  line-height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.shape-selected {
  color: #ffc107;
}

.text-background {
  color: #333;
  background-color: white;
}
.text-background--selected {
  color: #333;
  background-color: #ffc107;
}
